<div class="faq-container"><h2 class="ui header">常見問題</h2><div class="ui container"><form class="ui form"><div class="two stackable fields"><div class="field" style="max-width:150px;"><label>類別篩選</label><select class="ui dropdown" v-model="selectedCategory"><option value="全部">全部</option><option v-for="category in categories" :value="category.t">{{ category.t }}</option></select></div><div class="field"><label>關鍵字篩選</label><div class="ui icon input"><input type="text" v-model="searchKeyword" placeholder="搜尋常見問題..."><i class="search icon"></i></div></div></div></form><table class="ui celled collapsing table"><thead><tr><th>類別</th><th>問題</th><th>操作</th></tr></thead><tbody><tr v-for="(item, index) in filteredAndSortedFaqItems" :key="item.id"><td><i class="icon" :class="cataIcon(item.category)"></i><span v-html="highlightText(item.category)"></span></td><td><div v-html="highlightText(item.question)"></div></td><td><router-link class="ui green button" :to="'/ans/' + (item.id - 1)">查看回答<i class="chevron right icon"></i></router-link></td></tr></tbody></table></div></div>