<div class="ans"><div class="ui segment container" id="main-ans" v-if="faqItem"><router-link to="/faq"><h1 class="ui header center aligned">{{faqItem.question}}</h1><i class="huge orange icon" style="display:block; margin-left:auto; margin-right:auto" :class="cataIcon(faqItem.category)"></i></router-link><div class="ui divider"></div><div class="ui grid"><div class="row"><div class="column"><p class="description" v-html="highlightAndMakeBr(faqItem.answer, '')"></p></div></div><div class="row"><div class="eighteen wide right aligned column" v-if="faqItem &amp;&amp; faqItem.links &amp;&amp; faqItem.links.length"><div class="ui divider"></div><span v-for="(link, index) in JSON.parse(faqItem.links)"><div class="ui divider" v-show="index"></div><a :href="link.h" target="_blank" :title="link.t" rel="noopener noreferrer"><i class="globe icon"></i>{{link.t}}</a></span></div></div><div class="row"><div class="eight wide column text-left"><button class="ui blue button" @click="goBack"><i class="chevron left icon"></i>回上一頁</button></div><div class="eight wide right aligned column"><router-link class="ui orange button" to="/feedback">我有意見<i class="chevron right icon"></i></router-link></div></div></div></div></div>